<template lang="pug">
div.Telegram-page(ref="TelegramPage")
  Telegram-chat-item.animate__animated.animate__fadeInUp(
    v-for="(item, index) in messages",
    :key="index",
    :name="item.name",
    :name-color="item.nameColor",
    :time="item.time",
    :reply="item.reply",
    :type="item.type",
    :text-content="item.text",
    :srcs="item.srcs"
  )
</template>

<script>
import TelegramChatItem from '@/components/TelegramChatItem'
import sleep from '@/utils/sleep'
import { clearAllBodyScrollLocks } from 'body-scroll-lock'

const PerfectScrollbar = window.PerfectScrollbar

const messages = [
  {
    name: 'Denyan',
    type: 'join'
  },
  {
    name: 'Star River（找女m）',
    type: 'join'
  },
  {
    name: '鮑魚終結者',
    type: 'join'
  },
  {
    name: 'Mr.X',
    type: 'join'
  },
  {
    name: '小火柴',
    type: 'join'
  },
  {
    name: 'Xiang',
    type: 'join'
  },
  {
    name: 'Kuo kuo',
    type: 'join'
  },
  {
    name: '主席',
    type: 'join'
  },
  {
    name: '清心寡欲',
    type: 'join'
  },
  {
    name: '只意淫欧美妆',
    type: 'join'
  },
  {
    name: 'Tua',
    nameColor: '#D0D24B',
    type: 'text',
    text: '看了新闻来的。',
    time: '7:22 PM'
  },
  {
    name: 'Max Tan',
    nameColor: '#D25B4B',
    type: 'text',
    text: '慕名而来。',
    time: '7:38 PM'
  },
  {
    name: '风过 无痕',
    nameColor: '#9B9B9B',
    type: 'text',
    text: '都是看到这新闻进来的😂',
    time: '8:02 PM'
  }
]

export default {
  name: 'TelegramPage',
  components: {
    TelegramChatItem
  },
  data () {
    return {
      isActive: false,
      messages: [],
      index: 0
    }
  },
  methods: {
    async push () {
      this.messages.push(messages[this.index])
      await sleep(500)
      if (this.isActive) {
        this.$refs.TelegramPage.scrollTop = this.$refs.TelegramPage.scrollHeight
      }
      if (this.index < messages.length - 1) {
        this.index++
        this.push()
      } else {
        clearAllBodyScrollLocks()
      }
    }
  },
  mounted () {
    this.isActive = true
    this.push()

    const ps = new PerfectScrollbar(this.$refs.TelegramPage, {
      suppressScrollX: true,
      useBothWheelAxes: true
    })
    ps.update()
  },
  beforeDestroy () {
    this.isActive = false
    this.messages = []

    clearAllBodyScrollLocks()
  }
}
</script>

<style scoped lang="scss">
.Telegram-page {
  height: 100%;
  overflow: hidden;
  color: #FFF;
}
</style>
