          a.share-icon.facebook(href="https://www.facebook.com/sharer/sharer.php?u=https://theinitium.com/project/20220331-mainland-digital-sexual-violence/d.html%23t", target="_blank")
          a.share-icon.twitter(href="http://twitter.com/share?url=https://theinitium.com/project/20220331-mainland-digital-sexual-violence/d.html%23t&text=「一些男性對於女性有一種entitlement（權利），特別是對於自己有過性關係的女性，他們覺得這是我得回來的東西，他們覺得我有權，我擁有你，我想怎麼用（你的身體）都可以。」", target="_blank")
        p.title 「非人化」、厭女、男性特權：「盯射群」里的影像性暴力

        p.author 端傳媒記者 卓琳 實習記者 陸眉間
        div.article
          p 2021年12月27日，一篇題為《親手送我進N號房的兇手，是985大學的6年好友》的微信公眾號文章在中國大陸引發關注。文中的「我」發現自己400多張發佈在微博、微信朋友圈和私下發過給朋友的生活照被搬運到Telegram上的色情聊天群組，並加以意淫和侮辱。這名女生後來發現，盜取和搬運她照片的是她認識6年的男性好友。
          p 文章很快吸引了超過十萬人次的閱讀量，不少中國大陸讀者第一次發現，在Telegram、Twitter上存在著大量以簡體中文為交流語言、散播中國女生照片的色情群組、頻道和賬號。Twitter和Telegram分別於2009年和2015年在中國被禁，須「翻牆」才能登陸。
          p 在微博、微信等平台上，不少女性讀者對此非常憤怒，從前沉默的女性開始接受媒體採訪，訴說受害經歷。然而，端傳媒記者發現，這些發聲激起了更多男性的窺探欲，他們紛紛加入文中曝光的名為「盯射Chat」的Telegram群組。
          p 事件曝光之初，群裏不乏男性用戶自稱「慕名而來」、「看了新聞來的」。面對中國媒體的跟蹤報道和網民抨擊，有用戶在Telegram色情群裏發言：「她們是想連男生意淫的權利都剝奪嗎？你可以在網絡公開的（地）發照片，別人不能意淫？」、「自己拍婊子騷照放在你微博上，放在你朋友圈裡，還不讓別人對著你照片來一發？」、「難道女性yy（意淫）男男就不是對直男的傷害嗎？」
          p 香港嶺南大學社會學及社會政策系副教授陳效能對端傳媒表示，這些群組中的男性文化，蘊含了許多複雜因素：長久以來，男性一直習慣用女性身體來娛樂和消費，「這在他們看來是很正常的事情，」陳效能說，「反而是女性，如果你整日拿男性的裸體、性感照來談論，被人批評的機會是大很多的。」 而近年來，手機隨意拍攝的功能、Telegram能容納海量人數的聊天群組，都更加助長了這種色情文化。
          p 「一些男性對於女性有一種entitlement（權利），特別是對於自己有過性關係的女性，他們覺得這是我得回來的東西，他們覺得我有權，我擁有你，我想怎麼用（你的身體）都可以，」陳效能說，更深層的是，一些男性不覺得這是性暴力，默認女性的身體就是這樣用來消費的。
          div.img
            img(src="./../../assets/imgs/news-3-1.jpg")
            p 攝：Philippe PACHE/Gamma-Rapho via Getty Images
          h3 「非人化」女性和男性特權
          p 據<a href="https://www.bbc.com/news/world-60303769" target="_blank">BBC</a>、<a href="https://www.scmp.com/week-asia/long-reads/article/3135803/abuse-and-anger-inside-online-groups-spreading-stolen-sexual" target="_blank">SCMP</a>等媒體的調查報道，這類未經同意散播私密影像的色情群組，讓至少20個國家的女生受害。
          p 陳效能指出，根據近年來學術界和反性暴力倡議組織通用的概念，性自主、性同意是性暴力概念的核心。「任何未有consent（同意）、沒有對方同意做的和性有關的行為，其實都是性暴力。」 不過，在華人社會的語境中，人們常常誤認為，涉及實際肢體衝突的，才算性暴力。
          p 早於2017年，英國杜倫大學和肯特大學的法學教授Clare McGlynn和Erika Rackley曾發表論文，針對這類未經同意創作或傳播性私密影像的行為，提出「影像性暴力」（image-based sexual abuse）的概念。兩位學者指出，「影像性暴力」會催生助長更多性暴力形式出現的社會文化，並將導致大家漠視「性同意」。
          p 據端傳媒觀察，某些Telegram群組中，被盜發的多是女性發佈在公共平台上的日常照，以及穿著較為性感的生活照，也有小部分裸露私處的影像；另外也有一些群組會更直接地分享色情影像，群內傳播的大多數是直接對著女性私密部位拍攝的視頻或照片，其中有人會主動發出與性伴侶的性愛視頻，但更多人則是散布不知來歷的影像。
          p 群組中的用戶很喜歡鼓勵其他成員分享身邊女性的照片，或是自己主動發出女性照片供他人「欣賞」，常常在群中慫恿和詢問「有沒有出賣朋友讓我yy的」、「有人想射她嗎」、「有想玩她的嗎」。
          p 他們也常常以言語羞辱被曝光的女性，高頻詞是「母狗」、「賤狗」、「母豬」、「騷貨」等。面對身材較性感的女性，會評論「想跪著吸她的黑絲腳」、「這個騷母狗身材不錯」等；如果圖中女性不符合他們審美，會稱：「就是個醜逼母狗。」
          p 除了言語暴力，還有另一種對女性羞辱的方式——「屏射」，也稱「射屏」。一些群組成員將精液射到顯現女性照片的電子屏幕上，並將自己的生殖器與女生照片一起「合照」，這類人在群裏被稱為「射手」。
          p 關注性別議題的德國媒體心理學者劉拓接受端傳媒採訪時指出，上述「母狗」、「母豬」等用詞的背後是一種非人化（dehumanization）的心理，即通過物化女性來達到某種性暴力的行為。他進一步表示，言語羞辱的本質也是一種性暴力。
          p 劉拓指出，從動機來看，心理學上區分了兩種性暴力，工具型性暴力和表達型性暴力。對於前者而言，性是主要目的，這種情況下的「物化」是把女性作為性的工具。
          p 而表達型性暴力則是在認知層面上將人當成動物。「他們會表達出女性無法控制性慾，女性是低等的（觀點），這種女性是值得被控制和懲罰的。」劉拓說，在這種表達型的性暴力中，男性可能想像了某種特權感，並不是以實現性目的為主。他強調，大多情況下的性暴力都同時具有這兩種成份，有時工具型佔比更多，有時表達型佔比更多。
          div.img
            img(src="./../../assets/imgs/news-3-2.jpg")
            p 日本的色情電影拍攝現場。攝：Gerhard Joren/LightRocket via Getty Images
          h3 AV的訓練
          p 這類色情群組中，男性也有不同的性癖好，例如有人喜歡用「反差」、「反差婊」來形容女性，這類用語常伴隨著女性較為性感的日常照出現。他們通過想像照片中的女性在私下如何「騷」和「淫蕩」，如何與公眾場合的行為舉止形成「反差」來獲取快感。
          p 一個名為「收藏家」的Twitter賬號以發佈情侶或女性私密照而收穫了9000多位關注者，目前已被銷號。他向端傳媒記者表示，自己也喜歡「反差」這一類型。「就跟AV一樣，題材也分很多種，有些人就愛這一款。」他說，「我個人就是比較喜歡人前人後的對比反差感。」
          p 陳效能分析，這種「反差」偏好可能與一些男性長期看某一類型的AV有關。她目前正在進行一項男性偷拍他人的研究，該研究訪問了50多名因偷拍被發現和逮捕、並轉介至專門針對有性侵犯行為的輔導中心的男性。研究中，她詢問這些男性在網絡資源豐富的情況下為何仍想自己偷拍，其中一個原因是他們好奇女性裙底是什麼樣，內褲是否很性感，是不是表裡不一。
          p 陳效能指，一些受訪者對此有進一步反思，表示自己也喜歡看一類展現「反差」的AV。這類AV中，女主角常常被塑造為一個平時斯文、床上卻異常開放的形象。「長期以往這些男性就訓練出這種視角看女性。」陳效能說，「很多男性在大量AV的影響下，他們沒有意識到自己看女性的視角已經在這種影響下固定成特定的模式。」
          p 此外，群裏也有男性喜歡自稱「綠奴」、「綠帽俠」，他們會「貢獻」女朋友的照片給其他人進行意淫，以滿足自己的「綠帽幻想」。
          p 劉拓指出，綠帽幻想是一個很常見、比例也很高的心理現象，但這並不代表男性本人享受在現實中真的被「綠」。他表示，心理學上有很多種解釋，其中比較主流的是進化心理學中的精子競爭理論。「他看到其他男性在從事性行為過程中會提高他個人的性愉悅以及精子的質量，讓他更高地喚醒（性慾）。」劉拓說。
          div.img
            img(src="./../../assets/imgs/news-3-3.jpg")
            p 攝：Gavin Roberts/PC Plus Magazine via Getty Images
          h3 厭女文化和數位科技
          p 台灣國立政治大學傳播學院副教授方念萱對端傳媒表示，男性之所以敢於在群裏鑒賞、品評甚至詆毀女性，不擔心社會譴責，「在於（他們）認為社會上的性別腳本也和其價值相近，是羞辱所謂『蕩婦』、是厭女的。」 方念萱是長期關注性別暴力的學者，也是台灣「數位女力聯盟」成員。
          p 陳效能指出，總體而言，這些群組滋生了一種厭女文化，但可能從個體而言，其中的男性可能並不覺得自己是厭惡女性。「他們會覺得這是對女性的欣賞，她們能令他們興奮。」陳效能說，這種「欣賞」的視角是物化和性化的，「他們看不到他們正促成一種很大的厭女文化。」
          p 與以往個體私下幻想不同，通訊技術和社交平台促進了即時網絡社群的交流。在Telegram，用戶們動輒在數萬至十多萬人的群裏一起討論色情內容，這些群組人數龐大卻又擁有一定私密性。此外，Twitter上也存在許多發佈未經同意的私密照片的賬號。
          p 陳效能認為，男性對女性的偷窺、意淫一直存在，但科技讓這類事情變得更加輕易和泛濫。她同時指出，在互聯網上，陌生的群組成員之間互相鼓勵的氛圍也起到了火上澆油的效果；另外，隨意拍攝和分享的行為近年變得非常普遍，也使得一些人會潛移默化地將偷拍和發佈他人私密照的行為合理化，「變成不是『偷』（拍），而是『隨意拍』。」
          p 劉拓指出，任何未經同意下發佈的色情內容，都是一種犯罪。「我們既不希望看到政府一刀切，徹底全面地封殺這些平台，但也不希望任何個人受到傷害。」 劉拓說，「色情是人的本能，是沒有辦法完全杜絕的，解決事情的方向是，幻想可以有，但應該以無害的、不傷害人的方式去實現，比如正規的、有consent process的porn（經過同意之下製作的色情內容）。」
          p 方念萱也指出，面對社會譴責，加害人常常以「沒有傷害本人、純粹私下品評、洩欲」的理由來回應，或者以當事女性「不知不傷」或「她是蕩婦，羞辱自找」的邏輯為自己辯解，這套說辭的背後其實是不少網民覺得在網絡上人可以「無所不為無所不可」。
          p 長遠而言，陳效能說，社會需要更多地去討論性暴力和其文化，更敏感地認識到性暴力的核心是「未經同意」，並讓更多人意識到，影像性暴力的影響可能非常嚴重。
          p 「這些影像千秋萬世留在互聯網，可能十幾年後，她們的孩子會拿著影像來問她：媽咪，這個是不是你？當然這可能不會發生，但這種害怕和擔憂是不會消失的。」
      transition(
        enter-active-class="animate__animated animate__fadeInUp animate__fast",
        leave-active-class="animate__animated animate__fadeOutDown animate__fast",
      )
